html {
  height: 100%;
}
body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
/* Prevent text selection */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}
::-moz-selection {
  background-color: black;
  color: #fff;
}
::selection {
  background-color: black;
  color: #fff;
}
/* Reset */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, input, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
  font-size: 100%;
  font-family: 'Space Grotesk', Arial, sans-serif;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
/* Paper */

.border-left {
  border-left: black solid 1px;
}

.border-right {
  border-right: black solid 1px;
}

.border-top {
  border-top: black solid 1px;
}

.border-bottom {
  border-bottom: black solid 1px;
}
input[type=radio] {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
/* App */

.App {
  display: flex;
  flex-direction: column;
  height: 100%;
  text-align: center;
  background-color: white;
  color: black;
  overflow: hidden;
}

.App-logo {
  display: flex;
  padding: 0 20px 5px;
  font-size: 36px;
  font-weight: bold;
}

.App-header {
  min-height: 60px;
  height: 60px;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: 24px;
}

main {
  display: flex;
  flex-direction: row;
  height: calc(100% + -60px);
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  cursor: pointer;
}

.button div {
  display: flex;
}

.button:hover {
  background-color: black;
  color: white;
}

.button:hover svg {
  fill: white;
}

input:checked+label {
  background-color: black;
  color: white;
}

.DropBox {
	width: 100%;
	height: 100%;
	display: flex;
	align-items: flex-start;
}
.leftSide {
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: baseline;
    position: relative;
    overflow-y: auto;
    overflow-x: hidden;
}

.rightSide {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.editionMenu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 60px;
  text-align: center;
}

.save {
  display: flex;
  height: 100%;
  flex: 1 1;
}

.save__button {
  width: 60px;
}

.save__name {
  flex: 1 1;
  text-align: end;
  padding: 0 20px;
}

.save__format {
  display: flex;
}

.save__formatItem {
  width: 60px;
}

.displayCode {
  width: 60px;
}

.aboutContainerBackground {
  background-color: rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 998;
}
.aboutContainer {
  display: none;
  align-items: center;
  justify-content: center;
  background-color: white;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 999;

}

.about {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  cursor: pointer;
  width: 60px;
}
.canvasContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
  }  
.Workspace {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: baseline;
    position: relative;
    overflow-y: auto;
    overflow-x: hidden;
}
.Workspace__placeholder {
    position: relative;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: grey;
}
.Box, .BoxGroup {
  display: flex;
  flex-direction: column;
  margin: 20px;
  border: black solid 1px;
  background-color: white;
}

.Box__wrapper {
  position: relative;
}

.Box__title {
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  height: 60px;
  color: white;
  fill: white;
  background-color: black;
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.Box__titleIcon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
}

.Box__titleText {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1;
}

.Box__titleIcon+.Box__titleText {
  padding-right: 60px;
}

.Box__titleClose {
  position: absolute;
  right: -12px;
  top: -12px;
  cursor: pointer;
}

.Box__titleClose svg {
  fill: black;
  background-color: white;
  border-radius: 50%;
}

.Box__content {
  display: flex;
  flex-direction: column;
  min-width: 100px;
}

.Box .Box__content .Box__slider {
  width: 100px;
  width: calc(100% + -40px);
  min-width: 100px;
  margin: 20px;
} 

.Box__container {
  padding: 20px;
}

.Box__placeholder {
  color: grey;
}

/* Overwrite rc-slider style */
.Box .Box__content .Box__slider .rc-slider-handle {
  border: solid 2px black;
}
.Box .Box__content .Box__slider .rc-slider-handle:focus {
  border-color: black;
  box-shadow: none;
}
.Box .Box__content .Box__slider .rc-slider-handle-click-focused:focus {
  border-color: black;
  box-shadow: unset;
}
.Box .Box__content .Box__slider .rc-slider-handle:hover {
  border-color: black;
}
.Box .Box__content .Box__slider .rc-slider-handle:active {
  border-color: black;
  box-shadow: none;
}
.react-contextmenu-wrapper {
  width: 100%;
  height: 100%;
}

div+.DropBox>.react-contextmenu-wrapper {
  border-top: 1px solid black;
}

.react-contextmenu {
  display: flex;
  flex-direction: column;
  position: absolute;
  background-color: white;
  border: black 1px solid;
}

.react-contextmenu-item {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  height: 40px;
  cursor: pointer;
}

.react-contextmenu-submenu>.react-contextmenu-item {
  cursor: auto;
}

.react-contextmenu .react-contextmenu-item:hover {
  color: white;
  background-color: black;
}

.react-contextmenu .react-contextmenu .react-contextmenu-item {
  color: black;
  background-color: white;
}

.react-contextmenu .react-contextmenu .react-contextmenu-item svg {
  fill: black;
}

.react-contextmenu .react-contextmenu .react-contextmenu-item:hover {
  color: white;
  background-color: black;
}

.react-contextmenu .react-contextmenu .react-contextmenu-item:hover svg {
  fill: white;
}

.react-contextmenu-item>.react-contextmenu {
  display: none;
}

.react-contextmenu-item:hover>.react-contextmenu {
  display: flex;
}

.react-contextmenu-item:hover svg {
  fill: white;
}

.react-contextmenu-item:not(:last-child) {
  border-bottom: black 1px solid;
}

.react-contextmenu-itemLabel {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex: 1 1;
}

.react-contextmenu-itemIcon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
}

.react-contextmenu-itemText {
  padding: 10px;
}

.react-contextmenu-itemIcon+.react-contextmenu-itemText {
  padding-left: 0px;
}


















